body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-size: 16px;
  line-height: 20px;
  color: #252519;
}
pre {
  padding: 20px;
  margin: 0;
  background: #eee;
}
code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace;
  color: rgb(231, 76, 60);
}
a, a:visited {
    color: #261a3b;
}
h2 {
    font-size: 32px;
    line-height: 40px;
}
em {
    font-style: italic;
}

.App {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}

.App-main {
  display: flex;
  flex: 1 1;
  flex-direction: column;
  align-items: center;
  /* background-color: red; */
}

.App-footer {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-top: 10px;
  padding-bottom: 20px;
}

.Header {
  text-align: left;
  display: flex;
  flex-direction: row;
  align-items: center;
}

.Header a {
  text-decoration: none;
}

.Header-logo {
  width: 80px;
}

p.footer {
    color: grey;
}
p.footer a, p.footer a:visited {
    color: grey;
}

.Home {
  width: 420px;
}
.Home-header a {
  text-decoration: none;
}
.Home ul {
	list-style: none;
  padding: 0;
}

